/* montserrat-300 - cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  font-weight: 300;
  src: local(""),
    url("../fonts/montserrat-v18-cyrillic-300.woff2") format("woff2"),
    url("../fonts/montserrat-v18-cyrillic-300.woff") format("woff");
}
